<template>
  <div
    class="position-absolute full-change"
    @mouseenter="mouseenter"
    @mouseleave="mouseleave"
  >
    <img
      v-if="visible"
      class="img-suite cur-pointer"
      @click="changeFull"
      :src="
        require(`../../../assets/svg/dashboard/big-data/map/${
          isFull ? 'small' : 'full'
        }.svg`)
      "
      alt=""
    />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, reactive, toRefs } from "vue";
import { dashboardStore } from "@/store/modules/dashboard";
import sf from "screenfull";

export default defineComponent({
  name: "full-change",
  setup() {
    const data = reactive({
      visible: false
    });
    const isFull = computed(() => dashboardStore.isFull);
    return {
      ...toRefs(data),
      mouseenter() {
        data.visible = true;
      },
      mouseleave() {
        data.visible = false;
      },
      changeFull() {
        dashboardStore.setFull(!isFull.value);
        if (sf.isEnabled) {
          sf.toggle();
        }
      },
      isFull
    };
  }
});
</script>

<style scoped lang="less">
.full-change {
  top: 10px;
  right: 10px;
  z-index: 20;
  width: 80px;
  height: 80px;
}
</style>
