<template>
  <div
    class="data-select"
    :class="{
      'bk-area': type === 1,
      'bk-area__active': type === 1 && visible,
      'monitor-select': type === 2
    }"
    @click="changeSelect"
  >
    <span>{{ title }}</span>
    <AModal
      width="110rem"
      class="big-screen fixed-height"
      centered
      :visible="visible"
      @cancel="cancel"
      @ok="ok"
    >
      <template #title>
        <a-checkbox
          :indeterminate="dataSource.indeterminate"
          v-model:checked="dataSource.checked"
          @change="() => valueChange(dataSource, 1)"
        >
          {{ dataSource.name }}
        </a-checkbox>
      </template>
      <ul class="area-children">
        <li
          class="second-area"
          v-for="(i, index) of dataSource.children"
          :key="index"
        >
          <a-checkbox
            v-model:checked="i.checked"
            :indeterminate="i.indeterminate"
            @change="() => valueChange(i, 2)"
          >
            {{ i.name }}
          </a-checkbox>
          <ul class="area-children">
            <li class="third-area" v-for="(item, idx) of i.children" :key="idx">
              <a-checkbox
                v-model:checked="item.checked"
                @change="() => valueChange(item, 3)"
              >
                {{ item.name }}
              </a-checkbox>
            </li>
          </ul>
        </li>
      </ul>
    </AModal>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, PropType, reactive, toRefs } from "vue";
import { area, ISelectTree } from "./data-select.mock";

export default defineComponent({
  name: "data-select",
  emits: {
    dataChange: null
  },
  props: {
    type: {
      type: Number,
      default: 1
    },
    data: {
      type: Object as PropType<ISelectTree>,
      default: () => area
    },
    placeholder: {
      type: String,
      default: "神木市"
    }
  },
  setup(props, { emit }) {
    const data = reactive<{
      title: string;
      visible: boolean;
      dataSource: ISelectTree;
      selectedValue: string[];
    }>({
      title: props.placeholder,
      visible: false,
      dataSource: props.data,
      selectedValue: []
    });

    function cancel() {
      data.visible = false;
    }

    function ok() {
      let name = "";
      const selectValue: string[] = [];
      const nameL6 = () => name.length <= 6;

      function addName(item: ISelectTree) {
        if (item.checked && nameL6()) {
          name += name.length === 0 ? item.name : `,${item.name}`;
        }
      }

      function addValue(item: ISelectTree) {
        item.checked && selectValue.push(item.value);
      }

      // 一级直接加入值
      if (data.dataSource.checked) {
        addName(data.dataSource);
        addValue(data.dataSource);
      } else {
        // 二级优先加名字，加完之后三级才开始加
        // 值不分先后，都加进去
        // 如果选中或半选子类的值一并加进去
        data.dataSource.children?.forEach(i => {
          addName(i);
          addValue(i);
          if (i.indeterminate) {
            i.children?.forEach(ii => {
              addValue(ii);
            });
          }
        });
        // 如果加完之后，字体不足6，考虑第三层还没加过，有的话就加，够了就停
        if (nameL6()) {
          for (const i of data.dataSource.children as ISelectTree[]) {
            if (!i.checked) {
              for (const ii of i.children as ISelectTree[]) {
                addName(ii);
                if (!nameL6()) {
                  break;
                }
              }
            }
          }
        }
      }
      // 赋值
      data.selectedValue = selectValue;
      data.title = name.length > 6 ? name.slice(0, 6) + "..." : name;
      data.visible = false;
      emit("dataChange", data.selectedValue);
    }

    // 手动点击某个checkbox，先把点击会向下发生的事情处理完，再处理向上
    function valueChange(item: ISelectTree, level: number) {
      switch (true) {
        case level === 1: {
          item.children?.forEach(i => {
            i.checked = item.checked;
            i.children?.forEach(ii => (ii.checked = item.checked));
          });
          break;
        }
        case level === 2: {
          item.children?.forEach(ii => (ii.checked = item.checked));
          break;
        }
        default:
      }

      // 向上检查
      function checkState(i: ISelectTree) {
        if (i.children) {
          i.checked = i.children.every(ii => ii.checked);
          if (i.checked) {
            i.indeterminate = false;
          } else {
            i.indeterminate = i.children.some(
              it => it.checked || it.indeterminate
            );
          }
        }
      }

      // 检查二级后再检一级
      data.dataSource.children?.forEach(i => checkState(i));
      checkState(data.dataSource);
    }

    // 手动赋值，达到勾选和标题变化，可能会请求api
    function setValue(values: string[]) {
      // Map结构方便查询
      const valueMap = values.reduce(
        (res: Map<string, boolean>, cur) => res.set(cur, true),
        new Map()
      );

      // 如果存在的话，修改checked为true，同时删掉，方面匹配成功后结束
      function checkValue(i: ISelectTree) {
        let has = false;
        if (valueMap.size) {
          const value = i.value;
          has = valueMap.has(value);
          if (has) {
            valueMap.delete(value);
          }
        }
        return has;
      }
      if (valueMap.size) {
        data.dataSource.checked = checkValue(data.dataSource);
        // 如果是true，子类checked都改为true
        data.dataSource.checked && valueChange(data.dataSource, 1);
        if (valueMap.size) {
          data.dataSource.children?.forEach(i => {
            i.checked = checkValue(i);
            // 如果是true，子类checked都改为true
            i.checked && valueChange(i, 2);
            if (valueMap.size) {
              i.children?.forEach(ii => {
                // 最后一级就不需要了
                ii.checked = checkValue(ii);
              });
            }
          });
        }
      }
      ok();
    }
    onMounted(() => {
      // setValue(["sjc大柳塔煤矿",'jj']);
    });
    return {
      ...toRefs(data),
      changeSelect() {
        data.visible = true;
        // emit("areaChange", 111);
      },
      setValue,
      valueChange,
      cancel,
      ok
    };
  }
});
</script>

